}

.mobile-menu__button {
  min-width: 186px;
  margin: 0 auto 32px;
  padding-left: 48px;
  padding-right: 48px;
  font-family: DM Sans;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.mobile-menu__button:hover, .mobile-menu__button:focus {
  background-color: #ecebeb;
}

.mobile-menu__nav-item {
