}

.mobile-menu__nav-item:not(:last-child) {
  margin-bottom: 23px;
}

.mobile-menu__nav-item:before {
  content: "";
  height: 1px;
  width: 120%;
  background-color: #e18298;
  display: block;
  position: absolute;
  top: -10px;
  left: -20px;
}

.mobile-menu__nav-item:last-child:after {
  content: "";
  height: 1px;
  width: 120%;
  background-color: #e18298;
  display: block;
  position: absolute;
  top: 30px;
  left: -20px;
}

